.header {
  display: table;
  width: 258px;
  margin-left: 4px;
}

.header__row {
  display: table-row;
}

.header__title {
  display: table-cell;
  float: left;
  margin-bottom: 0px;
}

.header__intro {
  font-size: 10px;
  float: left;
  width: 150px;
}

.header__intro strong {
  margin-left: 0.3em;
}

.header .restart {
  background: #8f7a66;
  border-radius: 3px;
  padding: 0 20px;
  text-decoration: none;
  color: #f9f6f2;
  height: 35px;
  font-size: 10px;
  line-height: 35px;
  text-align: center;
  float: right;
  margin-top: 4px;
}

.header .restart:focus,
.header .restart:active,
.header .restart {
  border: none;
  border-style: none;
  outline: none;
}

.header .scores {
  display: table-cell;
  float: right;
}

.header .score {
  position: relative;
  display: inline;
  top: 25px;
  background: #bbada0;
  padding: 5px 20px;
  padding-top: 20px;
  margin-left: 5px;
  font-size: 12px;
  line-height: 47px;
  font-weight: bold;
  border-radius: 3px;
  color: white;
  text-align: center;
}

.header .score--current:after {
  content: "Score";
  position: absolute;
  width: 100%;
  top: 5px;
  left: 0;
  text-transform: uppercase;
  font-size: 10px;
  line-height: 10px;
  text-align: center;
  color: #eee4da;
}

.header .score--best:after {
  content: "Best";
  position: absolute;
  width: 100%;
  top: 5px;
  left: 0;
  text-transform: uppercase;
  font-size: 10px;
  line-height: 10px;
  text-align: center;
  color: #eee4da;
}

body {
  background: #faf8ef;
  color: #776e65;
}

.App {
  font-family: "Clear Sans", "Helvetica Neue", Arial, sans-serif;
  text-align: center;
  font-size: 25px;
  width: 268px;
  margin-left: auto;
  margin-right: auto;
}

.grid {
  display: block;
  width: 248px;
  height: 234px;
  background-color: #776e65;
  border: 10px #776e65 solid;
  border-radius: 5px;
  margin:10px auto;
}

.row {
  display: block;
  width: 248px;
  height: 50px;
  margin-bottom: 10px;
  margin-left: 3px;
}

.cell {
  display: block;
  width: 56px;
  height: 50px;
  margin-right: 6px;
  float: left;
  background: rgba(238, 228, 218, 0.35);
  border-radius: 2px;
}

.cell--2 {
  background-color: #eee4da;
}

.cell--4 {
  background-color: #ede0c8;
}

.cell--8 {
  color: #f9f6f2;
  background-color: #f2b179;
}

.cell--16 {
  color: #f9f6f2;
  background-color: #f59563;
}

.cell--32 {
  color: #f9f6f2;
  background-color: #f67c5f;
}

.cell--64 {
  color: #f9f6f2;
  background-color: #f65e3b;
}

.cell--128 {
  color: #f9f6f2;
  background-color: #edcf72;
}

.cell--256 {
  color: #f9f6f2;
  background-color: #edcc61;
}

.cell--512 {
  color: #f9f6f2;
  background-color: #edc850;
}

.cell--1024 {
  color: #f9f6f2;
  background-color: #edc53f;
}

.cell--2048 {
  color: #f9f6f2;
  background-color: #edc22e;
}

.cell__label {
  display: block;
  position: relative;
  top: 90%;
  margin-top: -50%;
}

.game-overlay {
  position: absolute;
  display: none;
  width: 268px;
  height: 255px;
  z-index: 100;
  background: rgba(238, 228, 218, 0.5);
}

.game-overlay.active {
  display: block;
}

.game-overlay--won {
  background: rgba(237, 194, 46, 0.5);
  color: #f9f6f2;
}

.game-overlay__message {
  margin-top: 75px;
}

.game-overlay__keep-going {
  margin-right: 9px;
}

.game-overlay__keep-going,
.game-overlay__try-again {
  background: #8f7a66;
  border-radius: 3px;
  padding: 0 20px;
  text-decoration: none;
  color: #f9f6f2;
  height: 40px;
  font-size: 12px;
  line-height: 42px;
  text-align: center;
}

.game-overlay__keep-going:focus,
.game-overlay__keep-going:active,
.game-overlay__keep-going,
.game-overlay__try-again:focus,
.game-overlay__try-again:active,
.game-overlay__try-again {
  border: none;
  border-style: none;
  outline: none;
}

.footer {
  width: 248px;
  margin-left: auto;
  margin-right: auto;
}

.footer__how-to-play,
.footer__attribution {
  font-size: 0.6em;
  padding-bottom: 1em;
}

.footer__how-to-play {
  border-bottom: solid 1px #776e65;
}

.footer__emphasized-text {
  font-weight: bolder;
  padding-right: 0.3em;
}

.footer__emphasized-text:not(:first-child) {
  padding-left: 0.3em;
}

.footer__link {
  font-weight: bolder;
  color: #776e65;
  padding-left: 0.3em;
  padding-right: 0.3em;
}

